import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { functionsEndpoint, production, paypalSandbox, paypalProduction } from '../../app/app.config';
declare var paypal

@Injectable()
export class PayProvider {

  constructor(
    private http: HttpClient
  ) { }

  generatePaymentUrl(model_id: string, request_id: string, title: string, amount: number, email: string, isProd: boolean = false) {
    return this.http.post<any>(`${functionsEndpoint}/pay/generate_payment`, {
      model_id,
      request_id,
      title,
      amount,
      email,
      isProd
    }).toPromise()
  }

  generatePaymentUrlZamnaPagos(control_id: string, pago_id: string, title: string, amount: number, email: string, isProd: boolean = false) {
    return this.http.post<any>(`${functionsEndpoint}/pay/generate_payment_zamna_pagos`, {
      control_id,
      pago_id,
      title,
      amount,
      email,
      isProd
    }).toPromise()
  }

  generatePaypalButton(el: string, amount: number, cb: (payment) => any) {

    paypal.Button.render({
      env: production ? 'production' : 'sandbox',
      commit: true,
      style: {
        color: 'gold',
        size: 'small'
      },
      client: {
        sandbox: paypalSandbox,
        production: paypalProduction
      },
      payment: function(_, actions: any) {
        return actions.payment.create({
          payment: {
            transactions: [
              {
                amount: { total: amount, currency: 'MXN' }
              }
            ]
          }
        })
      },
      onAuthorize: function(data, actions) {
        return actions.payment.execute().then(function(payment) {
          cb(payment)
        });
      },
    }, el)
  }

}



