import {
  Facebook
} from '@ionic-native/facebook';
import {
  BrowserModule
} from '@angular/platform-browser';
import {
  ErrorHandler,
  NgModule
} from '@angular/core';
import {
  IonicApp,
  IonicErrorHandler,
  IonicModule
} from 'ionic-angular';
import {
  SplashScreen
} from '@ionic-native/splash-screen';
import {
  StatusBar
} from '@ionic-native/status-bar';
import {
  GooglePlus
} from '@ionic-native/google-plus';

import {
  MyApp
} from './app.component';
import {
  BackProvider
} from '../providers/back/back';
import {
  HttpClientModule, HTTP_INTERCEPTORS
} from '@angular/common/http';
import {
  SocialLoginModule,
  AuthServiceConfig,
  GoogleLoginProvider,
  FacebookLoginProvider,
  AuthService,
} from "angular5-social-login";
import { TokenInterceptor } from './app.auth';
import { MomentModule } from 'ngx-moment';
import { AngularFireModule } from '@angular/fire';
import { AngularFireAuthModule } from '@angular/fire/auth';
import { AngularFirestoreModule } from '@angular/fire/firestore';
import { firebaseConfig } from './app.config';
import { AuthProvider } from '../providers/auth/auth';
import { PayProvider } from '../providers/pay/pay';
import { AngularFireStorageModule } from '@angular/fire/storage';

export function getAuthServiceConfigs() {
  let config = new AuthServiceConfig(
    [{
        id: FacebookLoginProvider.PROVIDER_ID,
        provider: new FacebookLoginProvider("313911162748778")
      },
      {
        id: GoogleLoginProvider.PROVIDER_ID,
        provider: new GoogleLoginProvider("1090173504117-gacr5s85vp9q1v2mtlgml92ica25s1nl.apps.googleusercontent.com")
      },
    ]
  )
  return config;
}

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    MomentModule,
    AngularFireModule.initializeApp(firebaseConfig.fire),
    AngularFireAuthModule,
    AngularFirestoreModule,
    AngularFireStorageModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {
      provide: ErrorHandler,
      useClass: IonicErrorHandler
    },
    {
      provide: AuthServiceConfig,
      useFactory: getAuthServiceConfigs
    },
    {provide: HTTP_INTERCEPTORS, useClass: TokenInterceptor, multi: true},
    AuthService,
    BackProvider,
    Facebook,
    GooglePlus,
    AuthProvider,
    PayProvider
  ]
})
export class AppModule {}



