import {
  HttpClient
} from '@angular/common/http';
import {
  Injectable
} from '@angular/core';
import {
  ToastController
} from 'ionic-angular';
import { Observable } from 'rxjs/Observable';
declare var io

@Injectable()
export class BackProvider {

  //public api: string = 'http://localhost:1337/api/v1'
  /* public api: string = 'https://zamnademy.com/api/v1' */
  public api: string = 'http://167.99.12.186:1337/api/v1'
  public admin: string = '5b69cb5d300d766aae22ac68'

  constructor(public http: HttpClient) {

  }

  isRole(role: string): boolean {
    if (!role) return false
    let user = JSON.parse(localStorage.getItem('user'))
    if (!user) return false
    if (!user.roles) return false
    return user.roles.indexOf(role) >= 0
  }

  // shorthands
  isA(): boolean {
    return this.isRole('ROLE_ADMIN')
  }
  isU(): boolean {
    return this.isRole('ROLE_USER')
  }

  get token() {
    return localStorage.getItem('token')
  }
  get uid() {
    return localStorage.getItem('uid')
  }
  get user() {
    return JSON.parse(localStorage.getItem('user'))
  }
  get isAdmin() {
    return this.isRole('ROLE_ADMIN')
  }

  get isViewPagos() {
    return this.isRole('ROLE_VIEW_PAGOS')
  }

  // auth
  login(email: string, password: string) {
    return this.http.post < any > (`${this.api}/user/login`, {
      email,
      password
    })
  }

  register(data) {
    return this.http.post < any > (`${this.api}/user/register`, data)
  }

  facebookApp(token: string) {
    return this.http.post < any > (`${this.api}/user/facebookApp`, {
      token
    })
  }

  googleApp(profile: any) {
    return this.http.post < any > (`${this.api}/user/googleApp`, {
      profile
    })
  }

  socialAuthWeb(data: any) {
    return this.http.post < any > (`${this.api}/user/socialAuthWeb`, data)
  }

  async reloadAuth() {
    const user = await this.getUserDetail(this.uid).toPromise()
    console.log(user);
    localStorage.setItem('user', JSON.stringify(user))
  }

  validate(data: any, fields: string[], toast: ToastController): boolean {

    if (!data) {
      toast.create({
        message: 'Los datos no pueden ser vacios...',
        duration: 2000
      }).present()
      return false
    }

    for (const field of fields) {
      if (!data[field]) {
        console.log(field);

        toast.create({
          message: `El campo "${field}" no puede estar vacío...`,
          duration: 2000
        }).present()
        return false
      }
    }

    return true

  }

  // ControlPago
  getUserControlesPago(uid: string) {
    return this.http.get < any[] > (`${this.api}/ControlPago?user=${uid}`)
  }
  getAllControles() {
    return this.http.get < any[] > (`${this.api}/ControlPago`)
  }

  getControlDetail(id: string) {
    return this.http.get<any>(`${this.api}/ControlPago/${id}`)
  } 

  addControlPago(control: any) {
    return this.http.post < any > (`${this.api}/ControlPago`, control)
  }
  updateControlPago(id: string, data: any) {
    return this.http.put < any[] > (`${this.api}/ControlPago/${id}`, data)
  }
  deleteControlPago(id: string) {
    return this.http.delete < any[] > (`${this.api}/ControlPago/${id}`)
  }
  createPagoZamna(controlId: string, amount: number, cardId: string, userId: string, sess: string, method: string, payment ? : any, plan?: number) {
    return this.http.post < any > (`${this.api}/ControlPago/crearPagoZamna`, {
      controlId,
      amount,
      cardId,
      userId,
      sess,
      method,
      payment,
      plan
    })
  }

  pagarPaynet(id: string, amount: number, desc: string, controlId: string, method: string) {
    return this.http.post<any>(`${this.api}/ControlPago/pagarPaynet`, {id, amount, desc, controlId, method})
  }

  restarAmount(id: string, amount: number, pago: string) {
    return this.http.put<any>(`${this.api}/ControlPago/restarAmount/${id}`, {amount, pagoId: pago})
  }

  // User
  updateUser(id: string, data: any) {
    return this.http.put < any[] > (`${this.api}/User/${id}`, data)
  }

  getUserDetail(id: string) {
    return this.http.get < any > (`${this.api}/User/${id}`)
  }

  // Cards
  getUserTarjetas(user: string) {
    return this.http.get < any[] > (`${this.api}/Tarjeta/getUserTarjetas/${user}`)
  }

  addTarjeta(data: any) {
    return this.http.post < any > (`${this.api}/Tarjeta/crearTarjeta`, data)
  }

  uploadImage(archivo: File) {
    const data = new FormData()
    data.append('image', archivo)
    return this.http.post < any > (`${this.api}/ControlPago/uploadImage`, data)
  }

  // Pago Zamna
  updatePagoZamna(id: string, data: any) {
    return this.http.put < any[] > (`${this.api}/PagoZamna/${id}`, data)
  }

  // Becas
  getUserBecas(id: string) {
    return this.http.get<any[]>(`${this.api}/BecaTeam/getUserTeams/${id}`)
  } 

  addBeca(data: any) {
    return this.http.post<any>(`${this.api}/BecaTeam`, data)
  }

  joinToTeam(userId: string, teamId: string) {
    return this.http.post<any>(`${this.api}/BecaTeam/joinToTeam`, {userId, teamId})
  }

  joinTeamByCode(code: string, userId: string) {
    return this.http.post<any>(`${this.api}/BecaTeam/joinTeamByCode`, {code, userId})
  }

  deleteBeca(id: string) {
    return this.http.delete<any[]>(`${this.api}/BecaTeam/${id}`)
  }

  getAllTeams(): Observable<any[]> {
    return this.http.get<any[]>(`${this.api}/BecaTeam`)
  }

  getTeamDetail(id: string): Observable<any> {
    return this.http.get<any>(`${this.api}/BecaTeam/${id}`)
  }

  sumarDeuda(id: string, amount: number): Observable<any> {
    return this.http.put<any>(`${this.api}/User/sumarDeuda`, {id, amount})
  }

}



